@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
* {
  -webkit-tap-highlight-color: transparent;
}

body {
  background: #f6f6fc;
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

input,
button,
textarea {
  font-family: "Poppins", sans-serif
}

a {
  color: #4263e5;
  text-decoration: none
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-wrapper {
  background: #fff;
  width: -webkit-fill-available;
  padding: 10px 0;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999
}

.header {
  float: left
}

.header a img {
  display: block;
  height: 43px;
  width: auto
}

.navigation {
  display: none;
  position: absolute;
  right: 0;
  top: 60px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2)
}

.navigation ul {
  list-style: none;
  padding: 0;
  margin: 0
}

.navigation ul .react-ripples {
  display: block !important
}

.navigation ul li a {
  font-size: 16px;
  color: #565867;
  text-decoration: none;
  display: block;
  padding: 12px 18px;
  white-space: nowrap;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.navigation ul li a:hover,
.navigation ul li a.active {
  background: #f6f6fc
}

.sign-nav {
  float: right
}

.sign-nav ul {
  list-style: none;
  padding: 0;
  margin: 0
}

.sign-nav ul li {
  float: left
}

.sign-nav ul li a {
  display: inline-block;
  font-size: 16px;
  color: #212121;
  text-decoration: none;
  border-radius: 16px;
  margin: 3px 6px 0 6px;
  padding: 6px 12px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.sign-nav ul li a.login {
  background: #4263e5;
  color: #fff
}

.sign-nav ul li a.login svg {
  fill: #fff
}

.sign-nav ul li a:hover {
  background: #f7f8ff
}

.sign-nav ul li a.login:hover {
  background: #4263e5;
  opacity: .8
}

.sign-nav ul li a svg {
  width: 16px;
  height: 16px;
  margin-right: 8px
}

.navigation-toggle {
  float: right;
  margin-left: 10px
}

.navigation-toggle svg {
  width: 20px;
  height: 20px;
  fill: #212121;
  cursor: pointer;
  border-radius: 100%;
  margin-top: 0px;
  padding: 10px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.navigation-toggle.active svg {
  background: #4263e5;
  fill: #fff;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg)
}

.container {
  width: 1000px;
  margin: 0 auto;
  position: relative
}

.content-wrapper {
  display: inline-block;
  width: -webkit-fill-available;
  clear: both;
  padding: 60px 0
}

.content-wrapper.first {
  margin-top: 65px;
}

.sign-wrapper {
  background: #fff;
  display: block;
  width: 300px;
  margin: 80px auto 20px auto;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2)
}

.sign-wrapper .title {
  padding: 27px;
  border-bottom: 1px solid #f7f8ff
}

.sign-wrapper .title h2 {
  font-size: 21px;
  margin: 0;
  line-height: 0
}

.sign-wrapper .form {
  padding: 20px
}

.sign-wrapper .form input[type="text"],
.sign-wrapper .form input[type="email"],
.sign-wrapper .form input[type="password"] {
  border: 1px solid #ededf3;
  outline: 0;
  padding: 12px 16px;
  border-radius: 3px;
  margin-bottom: 12px;
  font-size: 14px;
  display: block;
  width: -webkit-fill-available;
  box-sizing: border-box;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.sign-wrapper .form label[for="rememberme"],
.sign-wrapper .form label[for="subscribe"] {
  font-size: 14px;
  color: #565867;
  margin-left: 6px
}

.sign-wrapper .form button[type="submit"] {
  display: block;
  background: #4263e5;
  color: #fff;
  border: 0;
  outline: 0;
  font-size: 14px;
  line-height: 1em;
  padding: 12px 18px;
  border-radius: 3px;
  margin-top: 12px;
  cursor: pointer;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.sign-wrapper .form button[type="submit"]:hover {
  opacity: .85
}

.content-wrapper.featured-left .content,
.content-wrapper.featured-right .content {
  width: 470px;
  float: left
}

.content-wrapper.featured-right .content {
  float: right
}

.content-wrapper.featured-left .content h2,
.content-wrapper.featured-right .content h2 {
  font-size: 37px;
  font-weight: normal;
  margin: 0 0 20px 0;
  line-height: 1.3em
}

.content-wrapper.featured-left .content p,
.content-wrapper.featured-right .content p {
  font-size: 16px;
  line-height: 1.5em;
  color: #565867;
  margin: 0 0 12px 0
}

.content-wrapper.featured-left .content a.btn,
.content-wrapper.featured-right .content a.btn {
  display: inline-block;
  font-size: 17px;
  line-height: 1em;
  background: #4263e5;
  color: #fff;
  padding: 12px 24px;
  margin-top: 40px;
  border-radius: 4px;
  text-decoration: none;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .2);
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.content-wrapper.featured-left .content a.btn:hover,
.content-wrapper.featured-right .content a.btn:hover {
  opacity: .8
}

.content-wrapper.featured-left .image {
  float: right
}

.content-wrapper.featured-right .image {
  float: left
}

.content-wrapper.featured-left .image img {
  width: 420px;
  height: auto;
}

.features-wrapper,
.pricing-wrapper {
  background: #fff;
  text-align: center;
}

.content-wrapper h2.title {
  font-size: 37px;
  line-height: 1.4em;
  color: #212121;
  margin: 0 0 20px 0
}

.content-wrapper h2.title:after {
  content: '';
  background: #4263e5;
  width: 30px;
  height: 6px;
  border-radius: 3px;
  display: block;
  margin: 12px auto 0 auto
}

.content-wrapper p.description {
  font-size: 15px;
  line-height: 1.4em;
  color: #878996;
  width: 60%;
  margin: 0 auto 40px auto
}

.features-wrapper .features {
  width: 273.34px;
  min-height: 240px;
  background: #fff;
  margin: 0 10px 20px 10px;
  float: left;
  padding: 20px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
}

.features-wrapper .features svg {
  width: 30px;
  height: 30px;
  display: block;
  margin: 0 auto 20px auto;
  fill: #4263e5;
  background: #f7f8ff;
  padding: 20px;
  border-radius: 100%;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.features-wrapper .features:hover svg {
  background: #4263e5;
  fill: #fff
}

.features-wrapper .features h3 {
  font-size: 21px;
  font-weight: normal;
  line-height: 1.4em;
  margin: 0 0 12px 0
}

.features-wrapper .features p {
  font-size: 14px;
  line-height: 1.5em;
  color: #878996;
  width: -webkit-fill-available
}

.pricing-wrapper .pricing {
  background: #fff;
  width: 320px;
  float: left;
  margin-right: 20px;
  text-align: center;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
}

.pricing-wrapper .pricing.x3 {
  margin-right: 0
}

.pricing-wrapper .pricing .pricing-header {
  padding: 20px
}

.pricing-wrapper .pricing .pricing-header .icon svg {
  fill: #4263e5;
  border: 1px solid #4263e5;
  width: 37px;
  height: 37px;
  padding: 20px;
  border-radius: 100%;
  margin: 20px 0;
}

.pricing-wrapper .pricing.popular .pricing-header .icon svg {
  background: #4263e5;
  fill: #fff
}

.pricing-wrapper .pricing .pricing-header h2 {
  font-size: 21px;
  font-weight: normal;
  color: #212121;
  margin: 0
}

.pricing-wrapper .pricing .pricing-price {
  padding: 20px;
  background: #f7f8ff
}

.pricing-wrapper .pricing .pricing-price h4 {
  font-size: 32px;
  color: #565867;
  margin: 0
}

.pricing-wrapper .pricing .pricing-price h4.x-price {
  font-size: 17px;
  color: #a5a9c1;
  text-decoration: line-through;
  margin-bottom: 12px
}

.pricing-wrapper .pricing ul.pricing-features {
  list-style: none;
  padding: 0;
  margin: 20px
}

.pricing-wrapper .pricing ul.pricing-features li {
  font-size: 16px;
  color: #565867;
  margin: 0 0 12px 0
}

.pricing-wrapper .pricing .pricing-btn {
  padding: 20px 20px 40px 20px
}

.pricing-wrapper .pricing .pricing-btn a.btn {
  background: #4263e5;
  color: #fff;
  border: 1px solid #4263e5;
  font-size: 19px;
  text-decoration: none;
  padding: 12px;
  border-radius: 24px;
  display: block;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.pricing-wrapper .pricing .pricing-btn a.btn:hover {
  background: #fff;
  color: #4263e5
}

.testimonial-wrapper {
  text-align: center
}

.testimonial {
  width: 313.34px;
  float: left;
  text-align: left;
  margin: 0 10px 20px 10px
}

.testimonial .says {
  display: block;
  min-height: 100px;
  background: #fff;
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;
  background-position: bottom 20px right 20px;
  padding: 30px 20px;
  border-radius: 3px 3px 3px 0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
  margin-bottom: 40px;
  position: relative
}

.testimonial .says:after {
  content: '';
  width: 0;
  height: auto;
  border-top: solid 20px #fff;
  border-left: solid 18px transparent;
  border-right: solid 18px transparent;
  position: absolute;
  bottom: -20px;
  left: 0
}

.testimonial .says p {
  font-size: 15px;
  line-height: 1.5em;
  color: #565867;
  margin: 0 0 12px 0
}

.testimonial .author .img-profile {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  float: left;
  margin-right: 20px
}

.testimonial .author span {
  display: block
}

.testimonial .author .name {
  font-size: 17px;
  margin-bottom: 6px;
}

.testimonial .author .title {
  font-size: 14px;
  font-style: italic;
  color: #565867
}

.contact-wrapper {
  padding: 50px 0 60px 0
}

.contact-wrapper .container {
  background: #fff;
  width: 780px;
  padding: 30px;
  display: grid;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2)
}

.contact-wrapper h2.title {
  position: absolute;
  top: 0;
  left: 0;
  margin: -48px 0 0 0;
  font-size: 21px;
  padding: 10px 24px;
  background: #fff;
  box-shadow: -1px 0 2px 0 rgba(0, 0, 0, .075)
}

.contact-wrapper h2.title:after {
  content: none
}

.contact-left {
  background: #f6f6fc;
  width: 250px;
  float: left;
  padding: 20px;
  overflow: hidden
}

.contact-right {
  width: 470px;
  float: right
}

.contact-wrapper input,
.contact-wrapper textarea {
  display: block;
  border: 1px solid #ededf3;
  outline: 0;
  font-size: 15px;
  border-radius: 3px;
  padding: 12px 18px;
  margin-bottom: 20px;
  width: -webkit-fill-available;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.contact-wrapper button {
  background: #4263e5;
  color: #fff;
  font-size: 15px;
  padding: 10px 24px;
  border-radius: 3px;
  border: 0;
  outline: 0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
  cursor: pointer;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.contact-wrapper button:hover {
  opacity: .85
}

.newsletter {
  display: flex
}

.newsletter-wrapper {
  background: #4263e5;
  color: rgba(255, 255, 255, .65);
  padding: 20px 0
}

.newsletter-description {
  width: 400px;
  float: left
}

.newsletter-description h3 {
  font-size: 21px;
  color: #fff;
  margin: 0 0 12px 0
}

.newsletter-description h3:after {
  content: '';
  display: block;
  width: 30px;
  height: 3px;
  background: rgba(255, 255, 255, .2);
  margin-top: 6px
}

.newsletter-description p {
  font-size: 14px;
  line-height: 1.3em;
  font-style: italic
}

.newsletter-form {
  width: 550px;
  float: right;
  margin-top: 30px
}

.newsletter-form input,
.newsletter-form button {
  background: #fff;
  border: 0;
  border-radius: 3px;
  padding: 12px 16px;
  outline: 0;
  margin: 0 12px 0 0;
  font-family: 'Roboto', sans-serif;
  font-size: 15px
}

.newsletter-form button {
  background: rgba(255, 255, 255, .2);
  cursor: pointer;
  color: #fff;
  border: 0;
  margin: 0;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.newsletter-form button:hover {
  background: rgba(255, 255, 255, .15)
}

.footer-wrapper {
  background: #fff;
  color: rgba(0, 0, 0, .5);
  padding: 40px 0 0 0
}

.footer-about,
.footer-menu {
  float: left
}

.footer-about {
  width: 450px;
  font-size: 14px;
  line-height: 1.3em
}

.footer-menu {
  width: 150px;
  margin-left: 30px
}

.footer-menu h2.title {
  font-size: 19px;
  color: #212121;
  margin: 0
}

.footer-menu h2.title:after {
  content: '';
  display: block;
  background: #e2417a;
  margin: 8px 0;
  width: 30px;
  height: 2px;
  border-radius: 3px
}

.footer-menu.menu-2 h2.title:after {
  background: #41e09e
}

.footer-menu.menu-3 h2.title:after {
  background: #dde041
}

.footer-menu ul {
  padding: 0;
  list-style: none
}

.footer-menu ul li {
  padding: 5px 0;
}

.footer-menu ul li a {
  font-size: 16px;
  line-height: 1.3em;
  text-decoration: none;
  color: #565867;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.footer-menu ul li a:hover {
  color: #4263e5
}

.social-link {
  display: inline-block;
  width: -webkit-fill-available;
  margin: 12px 0
}

.social-link ul {
  list-style: none;
  margin: 0;
  padding: 0
}

.social-link ul li {
  float: left
}

.social-link ul li a {
  font-size: 16px;
  background: #4263e5;
  color: #fff;
  margin: 0 12px 0 0;
  padding: 6px 8px 3px 8px;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s
}

.social-link ul li a:hover {
  opacity: .85
}

.footer-credit {
  display: inline-block;
  width: -webkit-fill-available;
  font-size: 14px;
  line-height: 1.4em;
  color: #212121;
  padding: 20px 0;
  border-top: 1px solid #f6f6fc
}

.backtotop {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: #fff;
  font-size: 21px;
  font-weight: bold;
  line-height: 1em;
  color: #4263e5;
  padding: 10px;
  width: 23px;
  height: 23px;
  border-radius: 100%;
  cursor: pointer;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2)
}

.content-wrapper.page {
  padding: 60px 0 20px 0
}

.content-wrapper.page .container {
  width: 800px;
  background: #fff;
  padding: 20px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
  margin-top: 20px;
}

.content-wrapper.page h2.page-title {
  font-size: 32px;
  line-height: 1.4em;
  font-weight: normal;
  margin: 0 0 20px 0;
  padding-bottom: 12px;
  border-bottom: 4px solid #f7f8ff
}

.content-wrapper.page .page-content {
  font-size: 15px;
  color: #565867;
  line-height: 1.5em
}

@media screen and (max-width:800px) {

  .container {
    width: -webkit-fill-available;
    padding: 0 20px
  }

  .contact-wrapper .container,
  .contact-left,
  .contact-right {
    width: -webkit-fill-available;
    float: none;
    display: block
  }

  .contact-wrapper .container {
    margin: 0 20px
  }

  .contact-left {
    padding-bottom: 0;
    margin-bottom: 20px
  }

  .contact-wrapper h2.title {
    margin-top: -58px
  }

  .content-wrapper.page .container {
    width: -webkit-fill-available;
    margin: 20px 10px 0 10px
  }

  .navigation {
    right: 10px
  }

  .sign-nav ul li a .txt {
    display: none
  }

  .sign-nav ul li a svg {
    margin-right: 0
  }

  .content-wrapper.first {
    background-image: none;
    margin-top: 40px
  }

  .content-wrapper.featured-left .content,
  .content-wrapper.featured-right .content {
    float: none;
    width: -webkit-fill-available;
    display: block
  }

  .content-wrapper.featured-left .content h2,
  .content-wrapper.featured-right .content h2 {
    font-size: 27px
  }

  .content-wrapper.featured-left .content p,
  .content-wrapper.featured-right .content p {
    font-size: 15px;
    line-height: 1.4em
  }

  .content-wrapper.featured-left .image,
  .content-wrapper.featured-right .image {
    display: none
  }

  .content-wrapper h2.title {
    font-size: 27px
  }

  .content-wrapper p.description {
    width: -webkit-fill-available
  }

  .features-wrapper .features {
    width: -webkit-fill-available;
    min-height: auto;
    margin: 0 20px 20px 20px
  }

  .pricing-wrapper .pricing {
    width: -webkit-fill-available;
    margin: 0 0 20px 0
  }

  .testimonial {
    width: -webkit-fill-available;
    float: none;
    margin: 10px 20px 40px 20px
  }

  .testimonial .says {
    min-height: auto
  }

  .newsletter-description {
    width: -webkit-fill-available;
    float: none
  }

  .newsletter-form {
    float: none;
    width: -webkit-fill-available
  }

  .newsletter-form input {
    width: -webkit-fill-available;
    margin: 0 0 12px 0
  }

  .newsletter-form button {
    display: block;
    margin: 0
  }

  .footer-about {
    width: -webkit-fill-available;
    margin-bottom: 20px
  }

  .footer-menu {
    width: 48%;
    margin: 0 1%
  }

  .footer-menu h2.title {
    font-size: 19px
  }

  .sign-wrapper {
    width: -webkit-fill-available;
    margin: 80px 10px 20px 10px
  }

  .content-wrapper.page h2.page-title {
    font-size: 27px;
    line-height: 1.3em
  }

  .content-wrapper.page .page-content h1,
  .content-wrapper.page .page-content h2,
  .content-wrapper.page .page-content h3,
  .content-wrapper.page .page-content h4,
  .content-wrapper.page .page-content h5,
  .content-wrapper.page .page-content h6 {
    line-height: 1.3em
  }

}
